
import { defineComponent } from 'vue';
import HisFooter from "@/components/HisDynamicNavFooter.vue";
import { IonPage, IonLoading, modalController, IonToolbar, IonHeader } from "@ionic/vue"
import { Service } from "@/services/service"
import HisDate from "@/utils/Date"
import { toCsv, toPDFfromHTML } from "@/utils/Export"
import DrillPatientIds from '../../../../../components/DrillPatientIds.vue';
import { 
    sync,
    calendar
} from "ionicons/icons"
import { MultiStepPopupForm } from "@/utils/PopupKeyboard";
import { FieldType } from '@/components/Forms/BaseFormElements';
import { Option } from '@/components/Forms/FieldInterface'
import Validation from "@/components/Forms/validations/StandardValidations"
import { isPlainObject } from "lodash";
import dayjs from "dayjs";
import { generateDateFields } from "@/utils/HisFormHelpers/MultiFieldDateHelper"


    export default defineComponent({
        components: { IonPage, IonLoading, HisFooter, IonToolbar, IonHeader},
        data: () => ({
            logo: "/assets/images/login-logos/Malawi-Coat_of_arms_of_arms.png" as string,
            reportTitle: "Monthly MOH DHIS2",
            reportPeriod: "Jan 23 2023 - Oct 4 2023",
            btns: [] as Array<any>,
            date: HisDate.toStandardHisDisplayFormat(Service.getSessionDate()),
            apiVersion: Service.getApiVersion(),
            coreVersion: Service.getCoreVersion(), 
            isLoading: false as boolean,
            indicators: {
                type: Object,
                default: () => ({})
            },
            syncBtn: sync,
            calendarBtn: calendar,
            startDate: "",
            endDate: "",
            reportType: ""

        }),
        created() {
            this.btns = this.getBtns()
            MultiStepPopupForm([
                {
                    id: 'dhis2_report',
                    helpText: 'Select DHIS2 Report',
                    type: FieldType.TT_SELECT,
                    validation: (v: Option) => Validation.required(v),
                    computedValue: (v: Option) => v.value,
                    options: () => {
                        return [
                            {label: 'ANC Monthly Facility Report', value: 'ANC Monthly Facility Report'},
                            {label: 'HMIS-15', value: 'HMIS-15'},
                            {label: 'IDSR Monthly', value: 'IDSR Monthly'}
                        ]
                    }
                },
                {
                id: 'year',
                helpText: 'Select Year',
                type: FieldType.TT_NUMBER,
                computedValue: (v: Option) => v.value,
                validation: (v: Option) => {
                    const year = isPlainObject(v) ? v.value : -1
                    return Validation.validateSeries([
                        () => Validation.required(v),
                        () => {
                            if (isNaN(parseInt(`${year}`))) {
                                return ['Invalid year']
                            }
                            return null
                        },
                        () => Validation.rangeOf(v, 2000, HisDate.getYear(Service.getSessionDate()))
                    ])
                }
            },
            {
                id: 'month',
                helpText: 'Select Month',
                type: FieldType.TT_SELECT,
                validation: (v: Option) => Validation.required(v),
                computedValue: (v: Option) => v.value,
                options: () => {
                    return [
                        {label: 'January', value: '01'},
                        {label: 'February', value: '02'},
                        {label: 'March', value: '03'},
                        {label: 'April', value: '04'},
                        {label: 'May', value: '05'},
                        {label: 'June', value: '06'},
                        {label: 'July', value: '07'},
                        {label: 'August', value: '08'},
                        {label: 'September', value: '09'},
                        {label: 'October', value: '10'},
                        {label: 'November', value: '11'},
                        {label: 'December', value: '12'}
                    ]
                }
            }
            ], (f: any) => {
                this.startDate = `${f.year.value}-${f.month.value}-01`
                this.endDate = dayjs(new Date(this.startDate).toISOString()).endOf("month").format("YYYY-MM-DD")
                this.reportType = `${f.dhis2_report.value}`
                this.onPeriod()
                modalController.dismiss()
            })
        },
        methods: {
            async onPeriod() {
                this.reportPeriod = this.toDate(this.startDate) + " - " + this.toDate(this.endDate)
                this.reportTitle = this.reportTitle + ": " + this.reportType
                return null
            },
            toDate(date: string | Date) {
                if (date) {
                    return dayjs(date).format('DD/MMM/YYYY')
                }
                return date
            },
            async drilldown (title: string, patientIdentifiers: number[]) {
                (await modalController.create({
                    component: DrillPatientIds,
                    backdropDismiss: false,
                    cssClass: 'large-modal',
                    componentProps: {
                        title,
                        subtitle: this.reportPeriod,
                        patientIdentifiers,
                        onFinish: () => modalController.getTop().then(v => v && modalController.dismiss())
                    }
                })).present()
            },
            exportToCsv(){
                return null;
            },
            printSpec() {
            const content = document.getElementById('report-content')
            if (content) {
                toPDFfromHTML(`
                    <html>
                        <head>
                        <title>Print Cohort</title>
                        <style> 
                            .numbers {
                            width: 2.5%;
                            text-align: center;
                            border-width: 0px 1px 0px 0px;
                            border-style: dotted;
                            }
                            .row-title {
                            width: 17.75%;
                            text-align: left;
                            padding-left: 5px;
                            border-style: dotted;
                            }
                            .signatures {
                            width: 39.875%;
                            text-align: left;
                            padding-left: 5px;
                            border-style: dotted;
                            }
                            .row-name {
                            width: 39.875%;
                            text-align: left;
                            padding-left: 5px;
                            border-style: dotted;
                            border-left-style: solid;
                            }
                            .no-borders {
                            border-width: 0px;
                            }
                            #version-row td {
                            height: 30px;
                            }
                            #version {
                            text-align: right;
                            padding-right: 5px;
                            font-size: 10px;
                            }
                            #consistency_check div {
                            color: red;
                            padding-bottom: 10px;
                            }
                            a {
                            color: black;
                            text-decoration: none;
                            }
                            table {
                            width: 100%;
                            border-collapse: collapse;
                            margin-top: 10px;
                            }
                            tr {
                            height: 45px;
                            }
                            .vertical-separator {
                            border-width: 0px;
                            }
                            td {
                            border-style: solid;
                            border-width: 1px;
                            padding: 1em;
                            text-align: center;
                            }
                            .section-description td {
                            border-width: 0px;
                            }
                            .horisonatl-separator td {
                            border-width: 0px;
                            }
                            .numbers {
                            width: 2.5%;
                            text-align: center;
                            border-width: 0px 1px 0px 0px;
                            border-style: dotted;
                            }
                            .sum-arrows {
                            width: 75px;
                            height: 55px;
                            }
                            .postfixes {
                            font-size: x-small;
                            font-weight: bold;
                            position: relative;
                            top: -15px;
                            left: -40px;
                            }
                            .granules {
                            width: 100%;
                            height: 32px;
                            margin: 10px;
                            display: table;
                            }
                            .granules-row {
                            display: table-row;
                            }
                            .granules-cell {
                            display: table-cell;
                            text-align: center;
                            }
                            .granules span{
                            font-size: 10px;
                            }
                            .granules-right-td {
                            border-right-style: dotted !important;
                            border-right-width: 1px;
                            }

                            @media print
                            {
                            table { page-break-after:auto }
                            tr    { page-break-inside:avoid; page-break-after:auto }
                            td    { page-break-inside:avoid; page-break-after:auto }
                            thead { display:table-header-group }
                            tfoot { display:table-footer-group }
                            }
                        </style>
                        </head>
                        <body>
                        ${content.innerHTML}
                        </body>
                    </html>
                    `)
                }
            },
            regenerate(){
                return null;
            },
            getBtns() {
                return  [
                    {
                        name: "CSV",
                        size: "large",
                        slot: "start",
                        color: "primary",
                        visible: true,
                        onClick: () => this.exportToCsv()
                        },
                        {
                        name: "PDF",
                        size: "large",
                        slot: "start",
                        color: "primary",
                        visible: true,
                        onClick: () => this.printSpec()
                        },
                        {
                        name: "Finish",
                        size: "large",
                        slot: "end",
                        color: "success",
                        visible: true,
                        onClick: () => this.$router.push({ path:'/' })
                    }
                ]   
            },
        }
    })
